                    <template #editor="{data}">
                        <p-input-text v-model="data.btDate" />
                    </template>
                </p-column>
                <p-column header="BhCG" field="bhcg">
                    <template #editor="{data}">
                        <p-input-text v-model="data.bhcg" />
                    </template>
                </p-column>
                <p-column
                    :rowEditor="true"
                    style="width: 10%; min-width:8rem"
                    bodyStyle="text-align:center"
                ></p-column>
            </p-data-table>
        </p-panel>
    </div>
</template>

<script>
import { usePregnancyTestsInit } from "@/composable/pregnancy";

export default {
