        const { pregnancyTests, editingRows } = usePregnancyTestsInit();
        return { pregnancyTests, editingRows };
    }
};
</script>
<style scoped>
@media (max-width: 768px) {
    .datatable {
        width: 400px;
        overflow-x: auto;
        overflow-y: auto;
