<template>
    <div class="datatable">
        <p-panel header="Fetus History (Ultrasound Results)">
            <!-- <label class="p-mr-2"># Fetus</label>
            <p-input-text v-model="fetusInput" type="number" :disabled="true" /> -->

            <p-data-table
                :value="fetusHistories"
                datakey="fetusHistoryId"
                editMode="row"
                v-model:editingRows="editingRows"
                responsiveLayout="scroll"
                showGridlines
                class="p-datatable-sm p-mt-1 p-mb-2"
                :loading="loading"
                @row-edit-save="updateFetus"
                @row-edit-cancel="cancelUpdateFetus"
                @row-edit-init="keepOriginalFetuses"
            >
                <p-column header="Date" field="fetusDate">
                    <template #editor="{data}">
                        <p-calendar
                            v-model="data.fetusDate"
                            dateFormat="yy-mm-dd"
                            :showIcon="true"
                        />
                    </template>
                </p-column>
                <!-- <p-column header="Gestational Sac" field="gestationalSac">
                    <template #editor="{data}">
                        <p-input-number v-model="data.gestationalSac" />
                    </template>
                </p-column>
                <p-column header="Heart Beat" field="heartBeat">
                    <template #editor="{data}">
                        <p-input-number v-model="data.heartBeat" />
                    </template>
                </p-column> -->
                <p-column header="Gestational Sac" field="gestationalSac">
                    <template #editor="{data}">
                        <p-input-number v-model="data.gestationalSac" />
                        <small v-if="data.gestationalSac > 9" class="p-error">
                            Gestational sac value cannot exceed 9.
                        </small>
                    </template>
                </p-column>
                <p-column header="Heart Beat" field="heartBeat">
                    <template #editor="{data}">
                        <p-input-number v-model="data.heartBeat" />
                        <small v-if="data.heartBeat > 9" class="p-error">
                            Heart beat value cannot exceed 9.
                        </small>
                    </template>
                </p-column>
                <!-- <p-column header="Twins" field="twins">
                    <template #editor="{data}">
                        <p-input-number v-model="data.twins" />
                        <small v-if="data.twins > 9" class="p-error">
                            Twins value cannot exceed 9.
                        </small>
                    </template>
                </p-column> -->
                <p-column
                    header="Del"
                    style="width: 40px;"
                    bodyStyle="text-align: center;"
                >
                    <template #body="{data}">
                        <p-button
                            icon="pi pi-trash"
                            class="p-button-rounded p-button-danger p-button-text"
                            @click="deleteFetus(data)"
                            v-if="hasDeletePermission"
                        />
                    </template>
                </p-column>
                <p-column
                    :rowEditor="true"
                    style="width: 10%; min-width:8rem; color: green"
                    bodyStyle="text-align:center;"
                >
                </p-column>
                <p-column header="" field="createdBy" style="width: 8%; min-width:8rem;" bodyStyle="text-align:center;">
                    <template #body="{data}">
                        <span>{{ data.createdBy }}</span>
                    </template>
                </p-column>
                <template #footer class="p-grid">
                    <p-button
                        label="Create"
                        class="p-button-success p-mr-4"
                        @click="openCreateFetusDialog"
                        v-if="hasCreatePermission"
                    />
                </template>
            </p-data-table>
        </p-panel>
    </div>

    <pregnancy-create-fetus-history-dialog />
</template>

<script>
import { useFetusHistoriesInit } from "@/composable/pregnancy";
import { provide } from "vue";

import PregnancyCreateFetusHistoryDialog from "@/components/pregnancy/create-fetus-history-dailog.vue";
import { usePermissionCheck } from "@/composable/users";

export default {
    components: {
        PregnancyCreateFetusHistoryDialog
    },
    setup() {
        const {
            fetusHistories,
            editingRows,
            createFetusDialogDisplay,
            loading,
            // fetusInput,
            deleteFetus,
            updateFetus,
            cancelUpdateFetus,
            keepOriginalFetuses
        } = useFetusHistoriesInit();

        const {
            hasCreatePermission,
            hasDeletePermission
        } = usePermissionCheck();

        provide("createFetusDialogDisplay", createFetusDialogDisplay);
        provide("loading", loading);

        return {
            fetusHistories,
            editingRows,
            loading,
            openCreateFetusDialog: () => {
                createFetusDialogDisplay.value = true;
            },
            deleteFetus,
            updateFetus,
            cancelUpdateFetus,
            keepOriginalFetuses,
            // fetusInput,
            hasCreatePermission,
            hasDeletePermission
        };
    }
};
</script>
<style scoped>
@media (max-width: 768px) {
    .datatable {
        width: 400px;
        overflow-x: auto;
        overflow-y: auto;
        max-height: 400px;
    }
}
</style>
